import styled from 'styled-components';

const AppWrapper = styled.div`
  display: flex;
  flex: 1;
  justify-content: center;
  height: 100vh;
  background-color: #f1f1f9;
`;

const MobileFrame = styled.main`
  width: 100vh;
  max-width: 650px;
  height: 100vh;
  background-color: white;
  overflow: hidden;
  position: relative;
`;

export default {
  MobileFrame,
  AppWrapper
};



